<template>
  <div class="wrapper">
    <v-md-preview :text="base"></v-md-preview>
  </div>
  <div class="test-wrapper">
    <fx-switch v-model="value"></fx-switch>
    {{ value }}
  </div>
  <div class="wrapper">
    <v-md-preview :text="baseHtml"></v-md-preview>
  </div>
  <div class="test-wrapper">
    <fx-switch v-model="value1" activeColor="#13ce66" inactiveColor="#ff4949"></fx-switch>{{value1}}
  </div>
  <div class="test-wrapper">
    <fx-switch v-model="value2" activeColor="#2575e6" ></fx-switch>{{value2}}
  </div>
  <div class="wrapper">
    <v-md-preview :text="color"></v-md-preview>
  </div>
  <div class="wrapper">
    <v-md-preview :text="desc"></v-md-preview>
  </div>
  <div class="bottom"></div>
</template>

<script>
import {ref} from "vue";

export default {
  name: "aafxSwitch",
  setup() {
    const value = ref(false)
    const value1 = ref(false)
    const value2 = ref(false)

    const base = ref('# Switch 开关\n' +
        '开关选择器。\n' +
        '\n' +
        '## 何时使用\n' +
        '需要表示开关状态/两种状态之间的切换时。\n' +
        '## 基本用法\n' +
        '绑定 v-model 到一个 Boolean 类型的变量。\n')
    const baseHtml = ref('### 代码\n' +
        '``` html\n' +
        '<fx-switch v-model="value"></fx-switch>{{value}}\n' +
        '<script>\n' +
        'setup(){\n' +
        '    const value = ref(false)\n' +
        '    return { value } \n' +
        '}\n' +
        '</sc' + 'ript>\n' +
        '```\n' +
        '## 自定义开关颜色\n' +
        'activeColor 属性定义开关的值为 on 时的颜色，inactiveColor 开关的值为 off 时的颜色。')
    const color = ref('### 代码\n' +
        '``` html\n' +
        '<fx-switch v-model="value1" activeColor="#13ce66" inactiveColor="#ff4949"></fx-switch>{{value1}}\n' +
        '<fx-switch v-model="value2" activeColor="#2575e6" ></fx-switch>{{value2}}\n' +
        '<script>\n' +
        '  setup() {\n' +
        '    const value1 = ref(false)\n' +
        '    const value2 = ref(false)\n' +
        '    return{ value1 , value2 }\n' +
        '}\n' +
        '</scr'+'ipt>\n'+
'```')
    const desc = ref('## Switch 属性\n' +
        '|属性|说明|类型|可选值|\n' +
        '|-|-|-|-|\n' +
        '|v-model|绑定值|boolean|true/false|\n' +
        '|name|原生name属性|string|-|\n' +
        '|activeColor|switch的值为 on 时的颜色|string|-|\n' +
        '|inactiveColor|switch的值为 off 时的颜色|string|-|')
    return {
      value,value1,value2,
      base, baseHtml, color ,desc
    }
  }
}
</script>

